@import url(https://fonts.googleapis.com/css?family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css?family=Righteous|Roboto|Ubuntu&display=swap);
.landing {
    text-align: center;
    height: 100vh;
    min-height: 700px;
    background-image: -webkit-linear-gradient(301deg, #ffffff 40%, #c7c7c7 40%, #c7c7c7 50%, #ffffff 50%, #ffffff 90%, #c7c7c7 90%, #c7c7c7 100%);
    background-image: linear-gradient(149deg, #ffffff 40%, #c7c7c7 40%, #c7c7c7 50%, #ffffff 50%, #ffffff 90%, #c7c7c7 90%, #c7c7c7 100%);
    background-size: 1941.60px 1166.63px;
}

.landing > h1 {
    margin-top: 0;
    padding-top: 15%;
    font-size: 60px;
    font-family: 'Righteous', cursive;
    text-shadow: 6px 6px 0px rgba(0,0,0,0.2);
}

.resume-btn {
    border: solid 1px black;
    border-radius: 14px;
    background-color: #fff;
    color: #000;
    padding: 12px 30px;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    font-size: 15px;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.resume-btn:hover {
    background-color: #000;
    color: #fff;
}
.about-me {
    text-align: center;
    height: 50vh;
    min-height: 235px;
}

.about-me > h1 {
    text-shadow: 4px 4px 0px rgba(0,0,0,0.2);
}

.hr-about-me {
    height: 4px;
    background-color: #508ae5;
    border: none;
    border-radius: 40px;
    width: 100px;
}

.intro-text {
    color: #000;
    margin-top: 40px;
    font-size: 18px;
    z-index: 10;
}

.content-aboutme {
    margin: 0 auto;
    max-width: 55%;
    
}
.skills {
    text-align: center;
    height: 50vh;
    min-height: 360px;
}

.skills > h1 {
    text-shadow: 4px 4px 0px rgba(0,0,0,0.2);
}

.hr-skills {
    height: 4px;
    background-color: #fbde98;
    border: none;
    border-radius: 40px;
    width: 100px;
}

.content-skills {
    width: 55%;
    margin-top: 30px;
    text-align: left;
    display: inline-block;
}

.content-wrapper {
    margin-top: 40px;
    text-align: center;
}
.skills-category {
    float: left;
    color: #000;
    width: 100px;
    font-size: 1rem;
}

.skills-entry-wrapper {
    float: right;
}

.skills-entry {
    color: #000;
    margin-left: 10px;
    /* border: 1px solid black;
    border-radius: 4px;
    padding: 2px 2px; */
    font-size: 1rem;
}

.experience {
    text-align: center;
    height: 65vh;
    min-height: 500px;
}

.experience > h1 {
    text-shadow: 4px 4px 0px rgba(0,0,0,0.2);
}

.hr-experience {
    height: 4px;
    background-color: #ff6961;
    border: none;
    border-radius: 40px;
    width: 100px;
}

.content-experience {
    width: 55%;
    margin-top: 30px;
    text-align: left;
    display: inline-block;
}

.experience-wrapper {
    margin-top: 40px;
    text-align: center;
}
.experience-title {
   width: 400px;
   float: left;
}

.experience-entry-wrapper {
    float: right;
}

.content-experience {
    margin-top: 30px;
    color: #000;
    font-size: 1rem;
}
.contact {
    text-align: center;
    height: 70vh;
}

.contact > h1 {
    text-shadow: 4px 4px 0px rgba(0,0,0,0.2);
}

.hr-contact {
    height: 4px;
    background-color: #77dd77;
    border: none;
    border-radius: 40px;
    width: 100px;
}


.contact-entry-wrapper {
    display: inline-block;
    margin-top: 10px;
    text-align: center;
    width: 55%;
}

.contact-entry {
    display: block;
    margin-top: 30px;
}

.contact-entry > span > a {
    color: #000;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}



.contact-entry > span > a:hover {
    background-color: #000;
    color: #fff;
}
.navigation {
    font-family: 'Ubuntu', sans-serif;
    border-bottom: solid 1px #d8d8d8;
    z-index: 10;
    background-color: #fff;
}

.navigation > ul {
    list-style-type: none;
    text-align: center;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    margin-top: 0;
    padding-top: 16px;
}

.navigation ul > li {
    display: inline;
    margin: 0 30px;
}

.nav-link {
    text-decoration: none;
    font-weight: bold;
    color: #000;
}

.navigation ul > li > nav-link:visited {
    color: #000;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}
h1 {
    color: #000;
    font-family: 'Righteous', cursive;
}

span {
    font-family: 'Ubuntu', sans-serif;
    font-weight: bold;
}

p {
    font-family: 'Ubuntu', sans-serif;
}

body {
    background-color: #fff;
    margin: 0;
    padding: 0;
}

